import clienteAxios from '../_api/axios'

export const barcodeServices = {
    comparePrice
}

async function comparePrice(filters) {
    try {
        const { data } = await clienteAxios.post(`/barcode/compareprice/`, filters)
        return data
    } catch (error) {
        console.log(error)
        alert(error.message)
        return error
    }
}